.ripple__container {
  color: inherit;
  border-radius: inherit;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}
.ripple__animation {
  color: inherit;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  background: currentColor;
  opacity: 0;
  transition: 0.4s cubic-bezier(0, 0, 0.2, 1);
  pointer-events: none;
  overflow: hidden;
  will-change: opacity;
}
.ripple__animation--enter {
  transition: none;
}
.ripple__animation--visible {
  opacity: 0.15;
}
.pagination {
  list-style-type: none;
  display: inline-flex;
  align-items: center;
  padding: 0;
  margin: 0;
  height: 40px;
  align-items: center;
}
.pagination > li {
  align-items: center;
  display: flex;
}
.pagination a {
  transition: 0.3s cubic-bezier(0, 0, 0.2, 1);
}
.pagination a:hover {
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
}
.pagination--circle .pagination__item,
.pagination--circle .pagination__more,
.pagination--circle .pagination__navigation {
  border-radius: 50%;
}
.pagination--disabled {
  pointer-events: none;
  opacity: 0.6;
}
.pagination__item {
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  border-radius: 4px;
  color: rgba(0,0,0,0.87);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  height: 34px;
  width: 34px;
  margin: 0.3rem;
  text-decoration: none;
}
.pagination__item--active {
  box-shadow: 0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px rgba(0,0,0,0.14), 0 1px 10px rgba(0,0,0,0.12);
  background: #0070c5;
  color: #fff;
}
.pagination__navigation {
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: rgba(0,0,0,0.87);
  height: 2rem;
  border-radius: 4px;
  width: 2rem;
  margin: 0.3rem 15px;
}
.pagination__navigation .icon {
  font-size: 2rem;
  transition: 0.2s cubic-bezier(0.4, 0, 0.6, 1);
  vertical-align: middle;
  color: rgba(0,0,0,0.54);
}
.pagination__navigation--disabled {
  opacity: 0.6;
  pointer-events: none;
}
.pagination__more {
  margin: 0.3rem;
  display: inline-flex;
  align-items: flex-end;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.btn {
  align-items: center;
  background: #e0e0e0;
  border-radius: 25px;
  color: #000;
  display: inline-flex;
  height: auto;
  font-size: 16px;
  font-weight: bold;
  justify-content: center;
  margin: 0;
  min-width: 88px;
  outline: 0;
  padding: 9px 22px 10px;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: relative;
  vertical-align: middle;
}
.btn:after {
  border-radius: inherit;
  bottom: 0;
  content: '';
  left: 0;
  position: absolute;
  top: 0;
  right: 0;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.btn.btn--disabled {
  box-shadow: none !important;
  pointer-events: none;
  opacity: 0.4;
}
.btn.btn--disabled:not(.btn--loader) {
  color: rgba(0,0,0,0.3);
}
.btn__content {
  align-items: center;
  display: flex;
  flex: 1 0 auto;
  justify-content: center;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.btn--flat {
  background-color: transparent !important;
  box-shadow: none !important;
}
.btn--flat.btn--light:hover:after {
  background-color: rgba(255,255,255,0.12);
}
.btn--flat.btn--light.btn--disabled {
  color: rgba(255,255,255,0.26);
}
.btn--flat.btn--dark:hover:after {
  background-color: rgba(0,0,0,0.12);
}
.btn--flat.btn--dark.btn--disabled {
  color: rgba(0,0,0,0.3);
}
.btn--raised {
  will-change: box-shadow;
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
}
.btn--raised:active {
  box-shadow: 0 5px 5px -3px rgba(0,0,0,0.2), 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12);
}
.btn--icon {
  background: transparent;
  box-shadow: none !important;
  border-radius: 50%;
  justify-content: center;
  height: auto;
  width: auto;
  min-width: 0;
  padding: 0;
}
.btn--icon .icon {
  color: inherit;
}
.btn--icon:after {
  border-radius: 50%;
  opacity: 0.12;
}
.btn--icon:hover {
  border-radius: 50%;
}
.btn--icon:hover:after {
  background-color: currentColor;
  opacity: 0.12;
}
.btn--icon.btn--disabled {
  background-color: transparent !important;
  color: rgba(255,255,255,0.26) !important;
  pointer-events: none;
}
.btn--icon.btn--small {
  width: 28px;
}
.btn--icon.btn--large {
  width: 44px;
}
.btn--floating {
  border-radius: 50%;
  min-width: 0;
  height: 56px;
  width: 56px;
  padding: 0;
}
.btn--floating:after {
  border-radius: 50%;
}
.btn--floating .icon {
  height: 24px;
  width: 24px;
}
.btn--floating.btn--small {
  height: 40px;
  width: 40px;
}
.btn--floating.btn--small .icon {
  font-size: 18px;
  height: 18px;
  width: 18px;
}
.btn--floating.btn--large {
  height: 72px;
  width: 72px;
}
.btn--floating.btn--large .icon {
  font-size: 30px;
  height: 30px;
  width: 30px;
}
.btn--light {
  color: #fff;
}
.btn--light:hover:after {
  background-color: rgba(255,255,255,0.12);
}
.btn--light.btn--disabled:not(.btn--loader) {
  color: rgba(255,255,255,0.26) !important;
  opacity: 1;
}
.btn--light.btn--disabled:not(.btn--loader):not(.btn--flat):not(.btn--icon) {
  background-color: rgba(255,255,255,0.12) !important;
}
.btn--dark {
  color: rgba(0,0,0,0.87);
}
.btn--dark:hover:after {
  background-color: rgba(0,0,0,0.12);
}
.btn--dark.btn--disabled:not(.btn--loader) {
  color: rgba(0,0,0,0.3) !important;
  opacity: 1;
}
.btn--dark.btn--disabled:not(.btn--loader):not(.btn--flat):not(.btn--icon) {
  background-color: rgba(0,0,0,0.12) !important;
}
.btn--small {
  font-size: 13px;
  height: 28px;
}
.btn--large {
  font-size: 15px;
  height: 44px;
}
.btn--loader {
  pointer-events: none;
}
.btn--loader .btn__content {
  opacity: 0;
}
.btn__loading {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn__loading .icon--left {
  margin-right: 1rem;
  line-height: inherit;
}
.btn__loading .icon--right {
  margin-left: 1rem;
  line-height: inherit;
}
.btn--outline {
  border: 1px solid currentColor;
  background: transparent !important;
  box-shadow: none;
}
.btn--outline:hover {
  box-shadow: none;
}
.btn--block {
  display: flex;
  flex: 1;
  margin: 0 0;
  width: 100%;
}
.btn--round {
  border-radius: 28px;
}
.btn--round:after {
  border-radius: 28px;
}
.btn .icon--right {
  margin-left: 16px;
}
.btn .icon--left {
  margin-right: 16px;
}
.icon {
  align-items: center;
  display: inline-flex;
  font-size: 24px;
  justify-content: center;
  vertical-align: middle;
}
.icon--dark {
  color: rgba(0,0,0,0.54);
}
.icon--dark.icon--disabled {
  color: rgba(0,0,0,0.38);
}
.icon--light {
  color: #fff;
}
.icon--light.icon--disabled {
  color: rgba(255,255,255,0.5);
}
.icon--large {
  font-size: 2.5rem;
}
.icon--medium {
  font-size: 2rem;
}
.icon--x-large {
  font-size: 3rem;
}
.pagination {
  margin-top: 24px;
}
.pagination__item {
  border: 0;
  box-shadow: none;
}
.pagination__item:hover,
.pagination__item:active,
.pagination__item:focus {
  color: rgba(0,0,0,0.87);
}
.pagination__item--active {
  box-shadow: 0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px rgba(0,0,0,0.14), 0 1px 10px rgba(0,0,0,0.12);
}
.pagination__item--active:hover,
.pagination__item--active:active,
.pagination__item--active:focus {
  color: #fff;
  box-shadow: 0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px rgba(0,0,0,0.14), 0 1px 10px rgba(0,0,0,0.12);
}
.pagination__navigation {
  box-shadow: none;
  border: 0;
  margin-left: 0;
  margin-riht: 0;
}
.btn {
  text-transform: none;
  height: auto;
  font-weight: bold;
}
.btn--dark:hover:after {
  background-color: transparent;
}
a:hover .icon,
a:hover .material-icons {
  color: #0070c5;
}


